import React from "react";
import { Link } from "react-router-dom";

const MoviesPage = ({
  handleChange,
  value,
  handleSubmit,
  findedMovies,
  getDetails
}) => {
  return (
    <>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            className="searchFilminput"
            id="dynamic-label-input"
            type="search"
            placeholder="Search films"
            onChange={handleChange}
            value={value}
          />
          <label for="dynamic-label-input">Searching films</label>
          <button className="searchBtn" type="submit">
            Search
          </button>
        </div>
      </form>
      <ul>
        {console.log("here->", findedMovies)}
        {findedMovies.map(item => (
          <li key={item.id} onClick={getDetails}>
            <Link id={item.id} to={`/movies/${item.id}`}>
              {item.title}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
};

export default MoviesPage;
